<script lang="ts">
    export let users: User[];
    export let myId;
    export let changeVisibility;
    export let socket;
    export let isChatCreationMenuVisible;
    let message = "";

    function createChat(e) {
        message = "";
        const formData = new FormData(e.target);
        const chatName = formData.get('chat_name');
        let usersToAdd = []
        for (let i in users) {
            if (formData.get(users[i].id) == "true") {
                usersToAdd.push(users[i].id);
            }
        }
        if (usersToAdd.length == 0) {
            message = "select at least 1 user";
        } else {
            usersToAdd.push(myId);
            socket.emit("addChat", {usersToAdd: usersToAdd, chatName: chatName});
            changeVisibility();
        }
        document.addEventListener("keydown", (event) => {
            if (event.code == "Enter" && isChatCreationMenuVisible) {
                document.getElementById("createChatButton").click();
            }
        });
    }
</script>

<style>
    .window {
        background-color: white;
        position: fixed;
        left: 50%;
        top: 30%;
    }

    p {
        margin: 0px;
        padding: 0px;
    }

    p input[type="checkbox"] {
        margin-right: 5px;
    }

    input[type="text"] {
        width: 120px;
    }
</style>

    <div class="window">
        <form on:submit|preventDefault={createChat}>
            <p>
                <input id="createChatButton" type="submit" value="Create chat"/>
                <button
                        on:click={(e)=>{e.preventDefault(); changeVisibility()}}>X
                </button>
            </p>
            <input spellcheck="false" type="text" name="chat_name" placeholder="Chat name" required/>
            <p style="color:red">{message}</p>
            {#each users as user}
                {#if user.id !== myId }
                    <p><input type="checkbox" name={user.id} value={true}/>{user.name}</p>
                {/if}
            {/each}
        </form>
    </div>